import React, { useState, useEffect } from 'react';
import Cookies from 'universal-cookie';

const LoginForm = () => {
    const [dropdownOpen, setDropdownOpen] = useState(false);
    const [username, setUsername] = useState('');
    const [password, setPassword] = useState('');

    const [loggedInUser, setLoggedInUser] = useState('');

    useEffect(() => {
        const cookies = new Cookies();
        const usernameCookie = cookies.get('username');
        if (usernameCookie) {
            setLoggedInUser(usernameCookie.username);
        }
    }, []);

    const loginSubmit = async () => {
        const result = await fetch('/login', {
            'method': 'POST',
            body: JSON.stringify({ username, password }),
            headers: {
                'Content-Type': 'application/json'
            }
        });
        const body = await result.json();
        if (body.message === 'SUCCESS') {
            setDropdownOpen(false);
            const cookies = new Cookies();
            cookies.set('username', { username }, { path: '/' });
            setLoggedInUser(username);
        }
    };

    const logOut = async () => {
        const result = await fetch('/logout', {
            'method': 'POST',
            body: JSON.stringify({ username }),
            headers: {
                'Content-Type': 'application/json'
            }
        });
        if (result.ok) {
            const cookies = new Cookies();
            cookies.remove('username');
            setLoggedInUser('');
        }
    };

    return (
        <div className="login-section">
            <div style={{display: !loggedInUser ? 'none' : ''}}>
                {loggedInUser}
                <button onClick={() => logOut()}>Log out</button>
            </div>
            <button onClick={()=> setDropdownOpen(!dropdownOpen)} style={{display: !loggedInUser ? '' : 'none'}}>
                Login
            </button>
            <div className="login-form login-section" style={{display: dropdownOpen ? '' : 'none'}}>
                <input type="text" value={username} placeholder="Username or email" onChange={(event) => setUsername(event.target.value)} />
                <input type="password" value={password} placeholder="Password" onChange={(event) => setPassword(event.target.value)} />
                <button onClick={()=> loginSubmit()}>Submit</button>
                <a href="/">Create new user</a>
                <a href="/">Forgot password?</a>
            </div>
        </div>
    );
};

export default LoginForm;